<template>
    <p-data-table
        :value="demoContactsData"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        dataKey="id"
        v-model:selection="selectedRow"
        selectionMode="single"
        @row-select="onRowSelect"
    >
        <h3 style="padding: 5px 0px 5px 10px">Demographic Contacts</h3>
        <p-column-group type="header">
            <p-row>
                <p-column header="Demographic No" />
                <p-column header="Contact ID" />
                <p-column header="Contact Name" />
                <p-column header="Contact Type" />
                <p-column header="Created" />
                <p-column header="Updated At" />
            </p-row>
        </p-column-group>
            <p-column field="demographicNo" />
            <p-column field="contactId" />
            <p-column field="contactName" />
            <p-column field="contactType" />
            <p-column field="created" />
            <p-column field="updateDate" />

        <template #footer>
            <p-button
                label="Create"
                class="p-mr-2 p-button-success"
                @click="create"
            />
            <p-button label="Edit" class="p-mr-2" @click="editSelectedDemoContact"/>
            <p-button
                label="Delete"
                class="p-mr-2 p-button-danger"
                @click="deleteDemoContactConfirm"
            />
        </template>
    </p-data-table>
    <p-dialog
        v-model:visible="demoContactsAddDialogue"
        modal
        :header="dialogMode === 'Add' ? 'Add' : 'Edit'"
        :style="{ width: '30rem' }"
    >
        <div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="cycleId" class="p-col-4 p-mb-0">
                    Demographic No
                </label>
                <div class="p-col">
                    <p-input-text v-model="form.demographicNo" :disabled="true" />
                </div>
            </div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="number" class="p-col-4 p-mb-0">
                    Contact
                </label>
                <div class="p-col">
                    <p-auto-complete
                        v-model="form.contactId"
                        :suggestions="contactIdOpts"
                        @complete="contactIdSuggestions($event)"
                        field="label"
                        optionLabel="label"
                        optionValue="demographicNo"
                    />
                </div>
            </div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="donatedTo" class="p-col-4 p-mb-0">
                   Contact Type
                </label>
                <div class="p-col">
                    <p-dropdown
                        v-model="form.contactType"
                        style="width: 188px;"
                        :options="contactTypeOpts"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
            </div>
        </div>
        <div style="margin-top: 30px;">
            <p-button
                label="Save"
                class="p-button-success p-mr-2"
                @click="saveDemoContact"
            />
            <p-button
                label="Cancel"
                class="p-mr-2 p-button-outlined p-button-danger"
                @click="cancelDialogue"
            />
        </div>
    </p-dialog>
    <p-dialog
        v-model:visible="deleteDemoContactDialogue"
        modal
        header="Delete demographic contact !!"
        :style="{ width: '30rem' }"
    >
        <p>
            Are you sure you want to delete?
        </p>
        <p-button
            label="Yes"
            class="p-button-danger p-mr-2"
            @click="deleteDemoContact"
        />
        <p-button label="No" class="p-mr-2" @click="cancelDemoContactDelDialogue" />
    </p-dialog>
</template>

<script>
import { ref } from "vue-demi";
import { $axios } from "@/service/app-http";
import Service from "@/service/art-cycle.js";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";
import { onMounted } from "vue";
import { NL_FORM_BASE } from "@/constants";

export default {
    setup() {
        const store = useStore();
        const toast = useToast();
        const selectedRow = ref(null);
        const demoContactsAddDialogue = ref(false);
        const deleteDemoContactDialogue = ref(false);
        const dialogMode = ref("Add");
        const demoContactsData = ref([]);
        const contactIdOpts = ref([]);
        const cancelDialogue = () => {
            demoContactsAddDialogue.value = false;
        };
        const onRowSelect = (event) => {
            selectedRow.value = { ...event.data };
        };
        const contactIdSuggestions = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "F".toLowerCase()
            }).then((resp) => {
                contactIdOpts.value = resp.data;
            });
        };
        const fetchDemoContactData = async () => {
            if (store.state.artCycle.patient) {
                const patientId = store.state.artCycle.patient.id;
                try {
                    const response = await $axios.get(
                        `demographic-contact/list/${patientId}`
                    );
                    demoContactsData.value = response.data.list;
                } catch (error) {
                    console.error("Error fetching demographic contact data:", error);
                }
            }
        };
        const form = ref({
            id: 0,
            demographicNo: null,
            contactId: null,
            contactType: null,
        });
        const resetForm = () => {
            form.value = {
                id: 0,
                demographicNo: null,
                contactId: null,
                contactType: null
            };
        };
        const contactTypeOpts = [
            { label: "Partner", value: "Partner" },
            { label: "Husband", value: "Husband" },
            { label: "Wife", value: "Wife" },
        ];
        const create = () => {
            if (
                store.state.artCycle.patient &&
                store.state.artCycle.patient.id
            ) {
                dialogMode.value = "Add";
                resetForm();
                form.value.demographicNo =
                    store.state.artCycle.patient.id;
                    demoContactsAddDialogue.value = true;
            }
        };
        const saveDemoContact = () => {

            const payload = form.value;

            if (
                payload.contactId &&
                typeof payload.contactId === "object" &&
                payload.contactId.demographicNo
            ) {
                payload.contactId = payload.contactId.demographicNo;
            }

            if (form.value.id === 0) {
                $axios.post(`demographic-contact/add`, payload).then(() => {
                    fetchDemoContactData();
                });
            } else {
                $axios
                    .put(`demographic-contact/update/${form.value.id}`, payload)
                    .then(() => {
                        fetchDemoContactData();
                    });
            }
            
            toast.add({
                severity: "success",
                summary: "Success",
                detail: "Demographic contact saved successfully",
                life: 3000
            });

            resetForm();
            demoContactsAddDialogue.value = false;
        };

        const editSelectedDemoContact = () => {
            if (selectedRow.value) {
                form.value = {
                    id: selectedRow.value.id,
                    demographicNo: selectedRow.value.demographicNo,
                    contactId: selectedRow.value.contactId,
                    contactType: selectedRow.value.contactType
                };

                dialogMode.value = "Edit";
                demoContactsAddDialogue.value = true;
            }
        };

        const cancelDemoContactDelDialogue = () => {
            deleteDemoContactDialogue.value = false;
        }

        const deleteDemoContactConfirm = () => {
            deleteDemoContactDialogue.value = true;
        }

        const deleteDemoContact = async () => {
            if (selectedRow.value) {
                const idToDelete = selectedRow.value.id;
                try {
                    await $axios.delete(`demographic-contact/delete/${idToDelete}`);

                    await fetchDemoContactData();

                    selectedRow.value = null;
                    deleteDemoContactDialogue.value = false;

                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Demographic contact data deleted successfully",
                        life: 3000
                    });
                } catch (error) {
                    console.error("Error deleting demographic contact:", error);
                    toast.add({
                        severity: "error",
                        summary: "Error",
                        detail: "Failed to delete demographic contact",
                        life: 3000
                    });
                }
            }
        };

        onMounted(() => {
            fetchDemoContactData();
        });

        return {
            form,
            resetForm,
            demoContactsData,
            demoContactsAddDialogue,
            dialogMode,
            cancelDialogue,
            contactTypeOpts,
            create,
            selectedRow,
            editSelectedDemoContact,
            deleteDemoContactDialogue,
            cancelDemoContactDelDialogue,
            deleteDemoContactConfirm,
            fetchDemoContactData,
            demoContactsData,
            contactIdOpts,
            contactIdSuggestions,
            onRowSelect,
            saveDemoContact,
            deleteDemoContact
        };
    }
};
</script>

<style scoped></style>
